import React, {Component} from "react";
import "./Footer.scss"
import {APP_NAME} from "../../constants";


class Footer extends Component {

    state = {
        language: "en",
        availableLanguages: [
            {code: "en", title: "English"},
            {code: "zh", title: "Chinese"},
        ]
    };

    constructor(props) {
        super(props);


    }

    componentDidMount() {

        this.setLanguage();
    }

    setLanguage = () => {
        console.log(window.navigator.language);
        let browserLanguage = window.navigator.language;


    };

    render() {

        const {availableLanguages, language} = this.state;

        return (
            <div id="FooterComponent">

                <h2 className="color-white">{APP_NAME}</h2>

                <h4 className="color-white">Language</h4>

                <select value={language} onChange={this.setLanguage}>
                    {availableLanguages.map((lang, key)=>{
                        return <option key={key} value={lang.code} >{lang.title}</option>
                    })}
                </select>

            </div>
        )
    }
}

export default Footer;